import React from 'react';
import { Form, Input, Button, Checkbox, Col, Row, Card } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./Signup.css";

import LoginFooter from '../../LoginField/LoginFooter/LoginFooter';
import SignupHeader from '../SignupHeader/SignupHeader';












const Signup = () => {

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

    return (
      <>
             <SignupHeader />
             <div className="mainloginscreen signupfield">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Try Jobpatcher free for 14 days</h4>
                        <div className="boxfield">
                            <Row gutter={12}> 
                                <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "18px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="First" id="First" placeholder="First name"/>
                                            <div className="companyfield">
                                                <label for="First">First name</label>
                                            </div>                                     
                                        </div>
                                    </div>  
                                </Col>
                                <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "18px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="Last" id="Last" placeholder="Last name"/>
                                            <div className="companyfield">
                                                <label for="Last">Last name</label>
                                            </div>                                     
                                        </div>
                                    </div>                                    
                                </Col>                               
                                <Col xs={24} sm={24} md={24} lg={24} xl={24} style={{paddingTop: "18px"}}>
                                    <div class="field">
                                        <input type="text" name="Work" id="Work" placeholder="Work email"/>
                                        <div className="companyfield">
                                            <label for="Work">Work email</label>
                                        </div>                                     
                                    </div>
                                </Col>
                                <Col xs={24} sm={24} md={24} lg={24} xl={24} style={{paddingTop: "18px"}}>
                                    <div class="field">
                                        <input type="text" name="Phone" id="Phone" placeholder="Phone number"/>
                                        <div className="companyfield">
                                            <label for="Phone">Phone number</label>
                                        </div>                                     
                                    </div>
                                </Col>
                                
                                <Col xs={24} sm={24} md={24} lg={24} xl={24} style={{paddingTop: "18px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="Password" id="Password" placeholder="Password"/>
                                            <div className="companyfield">
                                                <label for="Password">Password</label>
                                            </div>                                     
                                        </div>
                                    </div> 
                                </Col>
                            </Row>   
        </div>

                        <Form.Item style={{paddingTop: "18px"}}>
                          <Link to="" type="primary" htmlType="submit" className="login-form-button">
                                GET STARTED
                          </Link>
                          <div className="signupfield">
                                By clicking on GET STARTED NOW you agree to our <Link>Terms of service </Link> and you 
                                acknowledge having read our <Link>Privacy policy.</Link> 
                          </div>                    
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default Signup;