import React from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./PasswordChange.css";
import LoginHeader from '../LoginHeader/LoginHeader';
import LoginFooter from '../LoginFooter/LoginFooter';












const PasswordChange = () => {

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

    return (
      <>
             <LoginHeader />
             <div className="mainloginscreen">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Password changed</h4>

                        <p className="pgcontent">You've successfully changed your password.</p>

                        <Form.Item>
                          <Link to="/" type="primary" htmlType="submit" className="login-form-button">
                                CONTINUE TO DASHBOARD
                          </Link>                  
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default PasswordChange;