import React from 'react'
import { Card, Input, Col, Row, Select, Checkbox,Collapse, Upload, Button  } from 'antd';
import { UploadOutlined } from '@ant-design/icons';

import "./CustomerAttachments.css";

import { MdArrowBack, MdMoreVert,MdAssignmentTurnedIn,MdAttachment } from 'react-icons/md';

import DragImg from "../../Assets/images/drag.png";


const { Panel } = Collapse;
const { TextArea } = Input;


const props = {
    action: '//jsonplaceholder.typicode.com/posts/',
    listType: 'picture',
    previewFile(file) {
      console.log('Your upload file:', file);
      // Your process logic. Here we just mock to the same file
      return fetch('https://next.json-generator.com/api/json/get/4ytyBoLK8', {
        method: 'POST',
        body: file,
      })
        .then(res => res.json())
        .then(({ thumbnail }) => thumbnail);
    },
  };



const CustomerAttachments = () => {
    return (
        <div className="boxfield notesfield attachementfield">
            <Collapse accordion >
                <span  className="notesicon">
                    <MdAttachment/>
                </span>                
                <Panel header="attachments  (0)" key="1">
                    <Upload {...props}>
                        <Button icon={<img src={DragImg} />}>Drag files here or <a> browse</a></Button>
                    </Upload>              
                </Panel>
            </Collapse>
        </div>
    )
}

export default CustomerAttachments;
