import React from 'react';
import { Row, Col,Modal, Input, Button, Select, Switch } from 'antd';

import "./EditCommunication.css";
import CommunicationsSettings from '../../CommunicationsSettings/CommunicationsSettings';


const { Option } = Select;


const EditCommunication = ({visible, handleCancel}) => {
    
    
    return (
        <div>
                <Modal
                    className="addnewaddressmodal editcommunication"
                    title="edit communication"
                    centered
                    visible={visible}
                    onOk={handleCancel}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        
                    <div className="addnewaddressmodal_section"> 
                    <div className="boxfield Communicationss-settings">
            {/* <Collapse accordion>
                <Panel header="Communications settings" key="1"> */}
                        <Row gutter={12}>
                            <Col span={24} className="communicationfield">
                                  <div className="communicationsection">
                                    <div className="swichsection">
                                        <div>
                                          <Switch checkedChildren="ON" unCheckedChildren="OFF" defaultChecked />
                                        </div>
                                        <div className="swichcontent">
                                            <h5>Email notifications</h5>
                                            <p>Notify your customer with automated emails</p>
                                        </div>                                      
                                    </div>
                                  </div>
                            </Col>
                            <div className="borderbreack"></div>
                              <Col span={24} className="communicationfield">
                                <div className="communicationsection">
                                    <div className="swichsection">
                                        <div>
                                            <Switch checkedChildren="ON" unCheckedChildren="OFF" />
                                        </div>
                                        <div className="swichcontent">
                                            <h5>SMS notifications</h5>
                                            <p>Notify your customer with automated SMSs</p>
                                        </div>                                      
                                    </div>
                                </div>
                              </Col>
                            <div className="borderbreack"></div>
                            <Col span={24} className="nameformat">
                                <label>Name format</label>
                                <Select defaultValue="Last name only" style={{ marginRight: '1.2rem' }}>
                                    <Option value="First and last name">First and last name</Option>
                                    <Option value="First name only">First name only</Option>
                                    <Option value="Last name only">Last name only</Option>
                                    <Option value="Title and full name">Title and full name</Option>
                                    <Option value="Title and first name">Title and first name</Option>
                                    <Option value="Title and last name">Title and last name</Option>
                                </Select> 
                            </Col>
                            <Col span={24} style={{marginTop: "21px"}} className="languageformat">
                                <label>Language</label>
                                {/* <TextArea rows={4} /> */}
                                <Select defaultValue="English - US" style={{ marginRight: '1.2rem' }}>
                                    <Option value="English - US">English - US</Option>
                                    <Option value="French">French</Option>
                                </Select> 
                            </Col>                            
                        </Row>
                {/* </Panel>
            </Collapse> */}
        </div>

                        <div class="businessaddressbtn buttonaddress"> 
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>  
                            <Button key="submit" type="primary" onClick={handleCancel}>
                                Save
                            </Button>                                          
                        </div>                                                                                                        
                    </div>
                        
                        
                </Modal>
        </div>
        
    )
}

export default EditCommunication;

