import React from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./NewPasswordFair.css";
import LoginHeader from '../LoginHeader/LoginHeader';
import LoginFooter from '../LoginFooter/LoginFooter';












const NewPasswordFair = () => {

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

    return (
      <>
             <LoginHeader />
             <div className="mainloginscreen">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Reset password</h4>

                        <div className="password_strength fair">
                          <div className="passwordTooltip">
                            <p><b>Please enter a stronger fair.</b> Try increasing the length of your password by using a multi-word phrase.</p>
                          </div>
                          <div className="password_area ">
                            <span></span>
                            <span></span>
                            <span></span>
                          </div>                          
                        </div>

                        <Form.Item
                          name="username"
                          rules={[{  required: true, message: 'We couldn’t find that email. Please try again' }]}
                        >
                            
                            <lable className="lablefield">New password</lable>
                            <Input placeholder="Enter password" type="password"/>
                        </Form.Item>  

                        <Form.Item
                          name="username"
                          rules={[{  required: true, message: 'We couldn’t find that email. Please try again' }]}
                        >
                            
                            <lable className="lablefield">Confirm your password</lable>
                            <Input placeholder="Enter password again" type="password"/>
                        </Form.Item>                       
                        

                        <Form.Item>
                          <Link to="/PasswordChange" type="primary" htmlType="submit" className="login-form-button">
                                CONTINUE
                          </Link>                   
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default NewPasswordFair;