import React from 'react'
import { Card, Input, Col, Row, Select, Checkbox,Collapse, Button, Menu, Dropdown  } from 'antd';
import "./CustomerNotes.css";

import { MdArrowBack, MdMoreVert,MdAssignmentTurnedIn, MdDeleteForever } from 'react-icons/md';

import Notes from "../../Assets/images/notes.png";
import { HiOutlineDotsHorizontal } from 'react-icons/hi';
import { Link } from 'react-router-dom';
import { BiExport } from 'react-icons/bi';
import { RiFolderDownloadLine, RiPencilLine } from 'react-icons/ri';
// import Editpencil from "../../Assets/images/editpencil.svg";





const { Panel } = Collapse;
const { TextArea } = Input;


const menu = (
    <Menu className="viewmenu">
      <Menu.Item key="0">
        <Link to=""> <RiPencilLine/>Edit</Link>
      </Menu.Item>
      <Menu.Item key="1">
        <Link to=""> <MdDeleteForever/>Delete</Link>
      </Menu.Item>
    </Menu>
  );


const CustomerNotes = () => {
    return (
        <div className="boxfield notesfield">
            <Collapse accordion >
                <span  className="notesicon">
                    <img src={Notes} />
                </span>                
                <Panel header="NOTES  (2)" key="1" className="notescollpacefield">
                    <TextArea rows={3} placeholder="Type note"/> 
                    <div class="notesbuttonfield"> 
                        <Button key="back">
                            Cancel
                        </Button>  
                        <Button key="submit" type="primary">
                            Save
                        </Button>                                          
                    </div>   
                    <ul className="notslist">
                        <li> 
                            <div className="notsheadesection">
                                <p><span>Today at 13:52</span><span className="notes_name">Peter Jonson</span></p>
                                <Dropdown overlay={menu} trigger={['click']}>
                                    <a onClick={e => e.preventDefault()}><HiOutlineDotsHorizontal/></a>                                       
                                </Dropdown>
                            </div> 
                            <p className="nots_content">Forth multiply gathered them he have dry upon bring after very forth over beast.</p>                                                        
                        </li>
                        <li> 
                            <div className="notsheadesection">
                                <p><span>Today at 13:52</span><span className="notes_name">Peter Jonson</span></p>
                                <Dropdown overlay={menu} trigger={['click']}>
                                    <a onClick={e => e.preventDefault()}><HiOutlineDotsHorizontal/></a>                                       
                                </Dropdown>
                            </div> 
                            <p className="nots_content">Gathered them he have dry upon bring.</p>                                                        
                        </li>
                    </ul>             
                </Panel>
            </Collapse>
        </div>
    )
}

export default CustomerNotes;
