import React from 'react';
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import PropertyDetails from '../../PropertyDetails/PropertyDetails';
import AdditionalPropertyDetails from "../../AdditionalPropertyDetails/AdditionalPropertyDetails";


import "./AddNewAddress.css";


const { Option } = Select;





const AddNewAddress = ({visible, handleCancel}) => {
    
    
    return (
        <div>
                <Modal
                    className="addnewaddressmodal"
                    title="ADD NEW ADDRESS"
                    centered
                    visible={visible}
                    onOk={handleCancel}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        
                    <div className="addnewaddressmodal_section"> 
                            <PropertyDetails /> 
                            <AdditionalPropertyDetails/>  

                        <div class="businessaddressbtn buttonaddress"> 
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>  
                            <Button key="submit" type="primary" onClick={handleCancel}>
                                Save
                            </Button>                                          
                        </div>                                                                                                        
                    </div>
                        
                        
                </Modal>
        </div>
        
    )
}

export default AddNewAddress;

