import React from 'react';
// import logo from './logo.svg';
import './App.css';
import 'antd/dist/antd.css';
import AddCustomer from './Components/AddCustomer/AddCustomer';
import Customers from './Components/Customers/Customers';
// import Header from "../src/Components/Header/header";
// import Header from '../Header/Header';
import Header from "../src/Components/Header/Header";
// import Footer from "../src/Components/Footer/Footer";
import Settings from './Components/Settings/Settings';
import CustomerProfile from './Components/CustomerProfile/CustomerProfile';
import Login from './Components/LoginField/Login/Login';

import {
  BrowserRouter,
  Switch,
  Route,
} from "react-router-dom";
import LoginHeader from './Components/LoginField/LoginHeader/LoginHeader';
import ResetPassword from './Components/LoginField/ResetPassword/ResetPassword';
import EmailSuccess from './Components/LoginField/EmailSuccess/EmailSuccess';
import NewPassword from './Components/LoginField/NewPassword/NewPassword';
import PasswordChange from './Components/LoginField/PasswordChange/PasswordChange';
import NewPasswordFair from './Components/LoginField/NewPasswordFair/NewPasswordFair';
import NewPasswordStrong from './Components/LoginField/NewPasswordStrong/NewPasswordStrong';
import Signup from './Components/SignupField/Signup/Signup';


function App() {
  return (
    <div className="App">
      <BrowserRouter>
          <Switch>
              {/* <Route path="/AddCustomer" component={AddCustomer}/> */}
              <Route path="/" exact={true} component={Settings}/>
              <Route path="/customers"   component={Customers}/>
              <Route path="/addcustomer" component={AddCustomer}/>              
              <Route path="/customerprofile" component={CustomerProfile}/>   
              <Route path="/login" component={Login}/>
              <Route path="/resetpassword" component={ResetPassword}/>
              <Route path="/emailsuccess" component={EmailSuccess}/>
              <Route path="/newpassword" component={NewPassword}/>
              <Route path="/passwordchange" component={PasswordChange}/>
              <Route path="/newpasswordfair" component={NewPasswordFair}/>
              <Route path="/newpasswordstrong" component={NewPasswordStrong}/>
              <Route path="/signup" component={Signup}/>

              
          </Switch>
        
      </BrowserRouter>
    </div>
  );
}

export default App;
