import React from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./ResetPassword.css";
import LoginHeader from '../LoginHeader/LoginHeader';
import LoginFooter from '../LoginFooter/LoginFooter';












const ResetPassword = () => {

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

    return (
      <>
             <LoginHeader />
             <div className="mainloginscreen">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Reset password</h4>

                        <p className="pgcontent">Enter the email address associated with your account and we'll send you a link to reset your password.</p>

                        <Form.Item
                          name="username"
                          rules={[{  required: true, message: 'We couldn’t find that email. Please try again' }]}
                        >
                            
                            <lable className="lablefield">Email address</lable>
                            <Input placeholder="Enter email" />
                        </Form.Item>                        
                        

                        <Form.Item>
                          <Link to="newpassword" type="primary" htmlType="submit" className="login-form-button">
                                CONTINUE
                          </Link>                          
                          <div className="signupfield">
                              Don't have an account? <Link href="">Sign up</Link>
                          </div>                    
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default ResetPassword;