import React, { useState } from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./NewPassword.css";
import LoginHeader from '../LoginHeader/LoginHeader';
import LoginFooter from '../LoginFooter/LoginFooter';












const NewPassword = () => {

  const [passwordStrength, setPasswordStrength] = useState(0);

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

  const hasNumber = value => {
    return new RegExp(/[0-9]/).test(value);
 }
 const hasMixed = value => {
    return new RegExp(/[a-z]/).test(value) &&
             new RegExp(/[A-Z]/).test(value);
 }
 const hasSpecial = value => {
    return new RegExp(/[!#@$%^&*)(+=._-]/).test(value);
 }

  const handlePasswordValidation = (e) => {
    let password = e.target.value;
    let passwordStr = 0;
    if(hasNumber(password)) {
      passwordStr++;
    }
    if(hasMixed(password)) {
      passwordStr++;
    }
    if(hasSpecial(password)) {
      passwordStr++;
    }
    if(password.length === 0){
      setPasswordStrength(0)
    }else {
      if(passwordStr < 2 || password.length < 5) {
        setPasswordStrength(1)
      }else if(passwordStr < 3 || password.length < 8){
        setPasswordStrength(2)
      }else if(passwordStr > 2 && password.length > 7){
        setPasswordStrength(3)
      }
    }
  }

  const renderPasswordStrength = () => {
    if(passwordStrength === 1) {
      return (
        <div className="password_strength weak">
          <div className="passwordTooltip">
            <p><b>Please enter a stronger password.</b> Try increasing the length of your password by using a multi-word phrase.</p>
          </div>
          <div className="password_area ">
            <span></span>
            <span></span>
            <span></span>
          </div>                          
        </div>
      )
    }
    if(passwordStrength === 2) {
      return (
        <div className="password_strength fair">
          <div className="passwordTooltip">
            <p><b>Please enter a stronger fair.</b> Try increasing the length of your password by using a multi-word phrase.</p>
          </div>
          <div className="password_area ">
            <span></span>
            <span></span>
            <span></span>
          </div>                          
        </div>
      )
    }
    if(passwordStrength === 3){
      return (
        <div className="password_strength strong">
          <div className="passwordTooltip">
            <p><b>Thank you for entering a strong password</b> </p>
          </div>
          <div className="password_area ">
            <span></span>
            <span></span>
            <span></span>
          </div>                          
        </div>
      )
    }
  }

    return (
      <>
             <LoginHeader />
             <div className="mainloginscreen">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Reset password</h4>

                        {
                          passwordStrength === 1 ?
                            (
                              <div className="password_strength weak">
                                <div className="passwordTooltip">
                                  <p><b>Please enter a stronger password.</b> Try increasing the length of your password by using a multi-word phrase.</p>
                                </div>
                                <div className="password_area ">
                                  <span></span>
                                  <span></span>
                                  <span></span>
                                </div>                          
                              </div>
                            ) : 
                            passwordStrength === 2 ?
                              (
                                <div className="password_strength fair">
                                  <div className="passwordTooltip">
                                    <p><b>Please enter a stronger fair.</b> Try increasing the length of your password by using a multi-word phrase.</p>
                                  </div>
                                  <div className="password_area ">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                  </div>                          
                                </div>
                              ): 
                              passwordStrength === 3 ?
                                (
                                  <div className="password_strength strong">
                                    <div className="passwordTooltip">
                                      <p><b>Thank you for entering a strong password</b> </p>
                                    </div>
                                    <div className="password_area ">
                                      <span></span>
                                      <span></span>
                                      <span></span>
                                    </div>                          
                                  </div>
                                ) : null
                        }

                        <Form.Item
                          name="username"
                          rules={[{  required: true, message: 'We couldn’t find that email. Please try again' }]}
                        >
                            
                            <lable className="lablefield">New password</lable>
                            <Input placeholder="Enter password" onChange={handlePasswordValidation} type="password"/>
                        </Form.Item>  

                        <Form.Item
                          name="username"
                          rules={[{  required: true, message: 'We couldn’t find that email. Please try again' }]}
                        >
                            
                            <lable className="lablefield">Confirm your password</lable>
                            <Input placeholder="Enter password again" type="password"/>
                        </Form.Item>                       
                        

                        <Form.Item>
                          <Link to="/PasswordChange" type="primary" htmlType="submit" className="login-form-button">
                                CONTINUE
                          </Link>                   
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default NewPassword;