import React from 'react';
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import PropertyDetails from '../../PropertyDetails/PropertyDetails';
import AdditionalPropertyDetails from "../../AdditionalPropertyDetails/AdditionalPropertyDetails";


import "./EditCustomerInfo.css";
import CustomerDetails from '../../CustomerDetails/CustomerDetails';


const { Option } = Select;





const EditCustomerInfo = ({visible, handleCancel}) => {
    
    
    return (
        <div>
                <Modal
                    className="addnewaddressmodal editcommunication width574 customerinfo_section"
                    title="edit customer info"
                    centered
                    visible={visible}
                    onOk={handleCancel}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        
                        <div className="addnewaddressmodal_section"> 
                            <CustomerDetails />

                            <div class="businessaddressbtn buttonaddress editcustomerinfo_btn"> 
                                <Button key="back" onClick={handleCancel}>
                                    Cancel
                                </Button>  
                                <Button key="submit" type="primary" onClick={handleCancel}>
                                    Save
                                </Button>                                          
                            </div>                                                                                                        
                        </div>
                        
                        
                </Modal>
        </div>
        
    )
}

export default EditCustomerInfo;

