import React from 'react';
import { Form, Input, Button, Checkbox, Row, Col } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./Login.css";
import LoginHeader from '../LoginHeader/LoginHeader';
import LoginFooter from '../LoginFooter/LoginFooter';












const Login = () => {

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

    return (
      <>
             <LoginHeader />
             <div className="mainloginscreen">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Sign in to your account</h4>
                        <div className="boxfield">
                            <Row gutter={12}> 
                                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="email" id="email" placeholder="Enter name"/>
                                            <div className="companyfield">
                                                <label for="email">Enter email</label>
                                            </div>                                     
                                        </div>
                                    </div>  
                                </Col>
                                <Col xs={24} sm={24} md={24} lg={24} xl={24} style={{paddingTop: "18px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="password" id="password" placeholder="Enter password"/>
                                            <div className="companyfield">
                                                <label for="password">Enter password</label>
                                            </div>                                     
                                        </div>
                                    </div>                                    
                                </Col>   
                            </Row>  
                          </div>
                        
                        <Form.Item className="rememberfield"  style={{paddingTop: "18px"}}>
                          <Form.Item name="remember" noStyle>
                            <Checkbox>Remember me</Checkbox>
                          </Form.Item>

                          <Link className="login-form-forgot" to="/resetpassword">
                            Forgot password
                          </Link>
                        </Form.Item>

                        <Form.Item>
                          <Link type="primary" htmlType="submit" className="login-form-button">
                              SIGN IN
                          </Link>
                          <div className="signupfield">
                              Don't have an account? <Link to="/signup">Sign up</Link>
                          </div>                    
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default Login;