import React from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import {Link} from "react-router-dom";

import { UserOutlined, LockOutlined } from '@ant-design/icons';


import "./EmailSuccess.css";
import LoginHeader from '../LoginHeader/LoginHeader';
import LoginFooter from '../LoginFooter/LoginFooter';












const EmailSuccess = () => {

  const onFinish = values => {
    console.log('Received values of form: ', values);
  };

    return (
      <>
             <LoginHeader />
             <div className="mainloginscreen">  
                  <div className="mainsubloginscreen">      
                    <div className="login_formfield">
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                      >
                        <h4>Check your inbox</h4>

                        <p className="pgcontent">Thanks, check your email for instructions to reset your password. Didn't get the email? Check your spam folder or </p>                                                                     
                        <Form.Item>                          
                          <div className="signupfield resendbtn">
                              <Link href="">Resend</Link>
                          </div>                    
                        </Form.Item>
                      </Form>

                      

              </div>
                    <LoginFooter/>
                  </div>      
             </div>
             
             
      </>
    )
}
export default EmailSuccess;