import React, {useState} from 'react';
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import PropertyDetails from '../../PropertyDetails/PropertyDetails';
import AdditionalPropertyDetails from "../../AdditionalPropertyDetails/AdditionalPropertyDetails";


import "./EditAdditionalInfo.css";
import CustomerDetails from '../../CustomerDetails/CustomerDetails';

import Edit from "../../../Assets/images/edit.svg";
import EditLeadSource from '../EditLeadSource/EditLeadSource';


const { Option } = Select;





const EditAdditionalInfo = ({visible, handleCancel}) => {
    
    const [leadvisible, setLeadVisible] = useState(false);

      const handleShowModalEdit = () => {
        setLeadVisible(true);         
      }
  
      const handleCloseModal = () => {
        setLeadVisible(false);
      }
    
    return (
        <div>
                <Modal
                    className="addnewaddressmodal editcommunication width447 customerinfo_section"
                    title="Edit Additional Info"
                    centered
                    visible={visible}
                    onOk={handleCancel}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        
                        <div className="addnewaddressmodal_section editaddtional_info"> 
                            <div className="boxfield Communicationss-settings">
                                <Row gutter={12}>
                                    <Col span={24} className="nameformat createdbyname">
                                        <label>Created by</label>
                                        <Input placeholder="John Doe" style={{background: '#f4f4f4'}}/>
                                    </Col>
                                    <Col span={24} style={{marginTop: "21px"}} className="nameformat createdbyname">
                                        <span className="editbtnpopup">
                                            <img src={Edit} />
                                        </span>
                                        <label>Profession</label>
                                        <Input placeholder="Business manager" /> 
                                    </Col>
                                    <Col span={24} style={{marginTop: "21px"}} className="nameformat">
                                        <span className="editbtnpopup">
                                            <img src={Edit} />
                                        </span>
                                        <label>Customer type</label>
                                        <Select defaultValue="Commercial" style={{ marginRight: '1.2rem' }}>
                                            <Option value="Commercial">Commercial</Option>
                                            <Option value="Residential">Residential</Option>
                                        </Select> 
                                    </Col>
                                    <Col span={24} style={{marginTop: "21px"}} className="languageformat">
                                        <span className="editbtnpopup"
                                            onClick={handleShowModalEdit}
                                        >
                                            <img src={Edit} />
                                        </span>
                                        <label>Lead source</label>
                                        {/* <TextArea rows={4} /> */}
                                        <Select defaultValue="Google adwords" style={{ marginRight: '1.2rem' }}>
                                            <Option value="Google adwords">Google adwords</Option>
                                            <Option value="French">French</Option>
                                        </Select> 
                                    </Col>                            
                                </Row>
                            </div>
                            <div class="businessaddressbtn buttonaddress"> 
                                <Button key="back" onClick={handleCancel}>
                                    Cancel
                                </Button>  
                                <Button key="submit" type="primary" onClick={handleCancel}>
                                    Save
                                </Button>                                          
                            </div>                                                                                                        
                        </div>
                        
                        
                </Modal>
                <EditLeadSource leadvisible={leadvisible} handleCancel={handleCloseModal} />
        </div>
        
    )
}

export default EditAdditionalInfo;

